import React,{useEffect} from 'react';
import uniqid from 'uniqid';
import './Stylesheet/app.css';
import { 
  Dashboard, Person, Dns, Description, Assessment, ShoppingBasket, 
  Inbox, Search, Backup, FilterList, ViewColumn, List, DragIndicator, MoreVert,
  KeyboardArrowLeft,KeyboardArrowRight, ToggleOff
} from '@material-ui/icons';

let isVisible = false, cur = 0, ref = 0, style = null;
const desc = "Some description goes here...";
const tags = "tag1, tag2, tag3, tag4, tag5";
const icons = (type) => {
  switch(type){
    case 'A':return <Dashboard className = "icons"/>
    case 'B': return <Person className = "icons"/>
    case 'C': return <Dns className = "icons"/>
    case 'D': return <Description className = "icons"/>
    case 'E': return <ShoppingBasket className = "icons"/>
    case 'F': return <Inbox className = "icons"/>
    case 'G': return <Assessment className = "icons"/>
  }
}

const highlight = (e, pos) =>{
  const items = document.getElementsByClassName("selected");
  for(let i = 0; i<items.length; i++){
    items[i].getElementsByClassName("border")[0].style.opacity = 0;
    items[i].getElementsByClassName("overlay")[0].style.opacity = 0; 
    items[i].parentNode.getElementsByClassName("contents")[0].style.color = "#8391A7";
  }
  const selected = (e != null) ?e.parentNode : items[pos - 1];
  selected.parentNode.getElementsByClassName("contents")[0].style.color = "white";
  selected.getElementsByClassName("border")[0].style.opacity = 1;
  selected.getElementsByClassName("overlay")[0].style.opacity = 0.05;
  
}

  const Navbar = () =>{
  const items = [
    ["Companies","A"],["Categories","B"],["Products","C"],
    ["Attributes","C"],["Country Mst","C"],["Packaging Mst","C"],
    ["Finance","C"],["Requesting for Quote","D"],["Orders","E"], 
    ["Currency","C"],["Metrics","C"],["Settings","B"], 
    ["Marketing","F"], ["Listing Services","G"]
  ]

  return(<div id = "navbar">
    {items.map((value, index)=>{return(<div className="row">
      <div className = "contents">{icons(value[1])}<span>{value[0]}</span></div>
      <div className = "selected" onClick = {e=> highlight(e.target)}>
        <div className = "border"/> <div className = "overlay" /> 
      </div>
    </div>)})}
  </div>);
}
const Header = () => {
  return(<div id = "header">
    <span id = "title">Categories</span>
    <div id = "others">
      <div id="searchBar">
        <Search id="icon"/> <input type="text" placeholder="Search..."/>
      </div>
      <Backup className = "items"/>
      <ViewColumn className = "items"/>
      <FilterList className = "items"/>
    </div>
    </div>);
}

const getTab = (direction) => {
  let width = window.innerWidth;
  const elems = document.getElementsByClassName("expand");
  const left = document.getElementsByClassName("left");
  const right = document.getElementsByClassName("right");
  for(let i = 0; i<left.length; i++)left[i].style.color = "black";
  for(let i = 0; i<right.length; i++)right[i].style.color = "black";
  cur += (direction === "right") ? -100 : 100;
  if(cur <= -300)for(let i = 0; i<right.length; i++)right[i].style.color = "gray";
  if(cur >= 0) for(let i = 0; i<left.length; i++)left[i].style.color = "gray";
  if(width > 900) return;
  if(cur > 0) {cur = 0; return;}
  if(cur < -300) {cur = -300; return;}
  for(let i = 0; i<elems.length; i++) elems[i].style.marginLeft = `${cur}%`;    
}

const Title = () =>{
  const ExpandTitle = () =>{
    const titles =["Category Name","Description","Tags","#Child"]
    return(<div className = "expand">
      {titles.map((value, index) => {return (<div className = "item">
        <KeyboardArrowLeft className = "left" onClick = {()=>getTab("left")}/>
        <span>{value}</span>
        <KeyboardArrowRight className = "right" onClick = {()=>getTab("right")}/>
      </div>)})}
    </div>);
  }
  return(<div id= "title">
  <div id = "empty"/>
  <div style = {{overflowX : "hidden"}}><ExpandTitle/></div>
  <span id = "active" className = "item">Active</span>
  
  </div>);
}

const enableDrag = (e)=> {
  let card = e.target;
  while(!card.classList.contains("cardContainer")) card = card.parentNode;
  card.setAttribute('draggable', true);
}

const dragStart = (e)=> {
e.target.classList.add("dragging");
ref = e.clientX;
 }

 const dragEnd = (e) => {
   const contains = document.getElementsByClassName("contains");
   for(let i = 0; i < contains.length; i++){
     const elem = contains[i].lastChild;
     if(elem.childNodes.length == 0) {
       changeType(contains[i],'B');
      contains[i].classList.remove("contains");
      }
   }
   const parent = e.target.parentNode;
   if(parent.id !== "tableContent"){
    changeType(parent.parentNode, "A");
    parent.parentNode.style.borderBottom = "0.3px solid #ADB7BB";
    parent.parentNode.classList.add("contains");
    changeType(e.target, 'C');
   }
   else changeType(e.target, 'B');
  
  e.target.setAttribute('draggable', false);
  e.target.classList.remove("dragging");
  ref = 0;
 }

 const dragOver = (e) => { 
    let body = e.target;
    while(body.id !== "tableContent") body = body.parentNode;
    const cards = document.getElementsByClassName("cardContainer");
    for(let i = 0; i< cards.length; i++){
      cards[i].style.borderBottom = "0.3px solid #ADB7BB";
    }
    const afterElement = getPosition(body, e.clientY);
    const card = document.querySelector(".dragging"); 
    if(e.clientX - ref < 50){
      try{
      if (afterElement == null) body.appendChild(card);
      else body.insertBefore(card, afterElement);
      }catch(err){console.log(err)}
    }
    else if(!card.classList.contains("contains")){
      try{
        const lastNode = (card == body.lastChild)? body.lastChild.previousSibling : body.lastChild;
        let node = (afterElement != null) ? afterElement.previousSibling: lastNode;
        if(node == card) node = card.previousSibling;
        node.style.borderBottom = "0.3px solid #2789E9";
        node.querySelector("#body").appendChild(card);
      }catch(err){console.log(err)}
    }
 }

 const getPosition = (container, y) => {
   const cards = [...container.querySelectorAll(".cardContainer:not(.dragging)")] 
   return cards.reduce((closest, child) => {
    const box = child.getBoundingClientRect();
    const offset = y - box.top - box.height / 2;
    if (offset < 0 && offset > closest.offset) return { offset: offset, element: child }
    return closest;  
  }, { offset: Number.NEGATIVE_INFINITY }).element;
}
const ExpandContent = (props) => {
  return(<div className = "expand ">
    <div className ="superContainer">
      <div className = "container" >
        <img id = "image" 
        src = {require(`./Resources/Images/Assets/${props.name}.png`)} draggable = {false}/>
        </div> 
      <div className = "container"><span className = "item">{props.name}</span></div>
    </div>
    <div className = "container"><span className = "item">{props.description}</span></div>
    <div className = "container"><span className = "item">{props.tags}</span></div>
    <div className = "container" id = "child"><span className = "item">{props.child}</span></div>
    </div>);
}

const changeType = (card, type) => {
  const drag = card.firstChild.firstChild.firstChild.firstChild;
  const drop = card.firstChild.firstChild.firstChild.nextSibling.firstChild;
  switch(type){
    case "A":   drag.style.marginLeft = "0px";  drop.style.opacity = 1; break;
    case "B":   drag.style.marginLeft = "0px"; drop.style.opacity = 0; break;
    case "C":   drag.style.marginLeft = "30px"; drop.style.opacity = 0; break;
  }  
}

const dropCards = (e, flag) => {
  const container = e.parentNode.parentNode.parentNode.parentNode.lastChild;
  if(flag != null) {
    e.style.transform = (flag) ? "rotate(90deg)" : "rotate(0deg)";
    container.style.display = (flag) ? "block" : "none";
  }
  else{
    flag = e.style.transform == "rotate(90deg)";
    e.style.transform = (flag) ? "rotate(0deg)" : "rotate(90deg)";
    container.style.display = (flag) ? "none" : "block";
  }
}
  
const Card = (props) => {
  return(<div id = {props.id} className = "cardContainer" onDragStart = {e =>dragStart(e)} onDragEnd = {e => dragEnd(e)}>
    <div className = 'card'>
    <div className = "superContainer">
      <div className = "container"> 
        <DragIndicator id ="drag" onMouseDown = {e => enableDrag(e)}/>
      </div>
      <div className = "container">
        <img id = "drop" onClick = {e => dropCards(e.target)}
        src = {require("./Resources/Images/Icons/drop.png")} draggable = {false}/>
      </div>
    </div>
    <div style = {{overflowX : "hidden"}}>
    <ExpandContent name = {props.name} description = {props.description} tags = {props.tags} child = {props.child}/>
    </div>
    <div className = "superContainer" id = "lastCol">
      <div className = "container">
        <input className = "switch" id = {props.id + "switch"} 
        type="range" min="0" max="1" onClick = {() => toggle()}/>
      </div>
      <div className = "container" ><MoreVert id = "option"/></div> 
    </div>
    </div>
    <div id = "body"/>
  </div>);
}

const toggle = () => {
style.textContent = "";
const temp = document.getElementsByClassName("switch");
  for(let i = 0; i< temp.length; i++){
    let bg = (temp[i].value == 1) ? "#BFDCF9" : "#D3D3D3";
    let fg = (temp[i].value == 1) ? "#2789E9" : "#E2EDF2";
    temp[i].style.backgroundColor = bg;
    style.textContent += `#${temp[i].id}::-webkit-slider-thumb{background-color: ${fg}}`;
  }
}

const reArrange = () => {
  let navbar = document.getElementById('navContainer');
  let canvas = document.getElementById('canvas');
  let topbar = document.getElementById('topbar');
  let width = window.innerWidth;
  navbar.style.left = (width < 1600) ? "-100%" : "0";
  if(width >= 1600) isVisible = false;
  if(width >= 900) {cur = 0;getTab("left");}
  let val = (width < 1600) ? "50%" : "80%";
  canvas.style.left = val;
  canvas.style.transform = `translate(-${val}, 0%)`; 
  topbar.style.opacity = (width < 1600) ? "1" : "0";
}

const viewNav = () => {
  let navbar = document.getElementById('navContainer');
  navbar.style.left = (isVisible) ? "-100%" : "0";
  isVisible = !isVisible;
}

export const App = () => {
  useEffect(()=> {
    style = document.createElement("style");
    document.head.appendChild(style);
    toggle(); reArrange(); getTab("left"); highlight(null,2);
    const cards = document.getElementsByClassName("cardContainer");
    for(let i = 0; i<cards.length; i++){
      changeType(cards[i],'B');
      const drop = cards[i].firstChild.firstChild.lastChild.firstChild;
      dropCards(drop, false);
    } 
  },[])
  window.addEventListener('resize', () => reArrange());
  return ( <div id = "app">
    <div id="topbar"><List id = "icon" onClick = {()=>{viewNav()}}/></div>
    <div id = "navContainer"><Navbar/></div>
    <div id = "canvas">
      <div id = "top">
        <span>Categories</span> <button>+ Add new category</button>
      </div>
      <div id = "table">
        <Header/><Title/>
        <div id = "tableContent" onDragOver = {e => dragOver(e)}>
          <Card id = {uniqid()} name = "Granite" description = {desc} tags = {tags} child = "3"/>
          <Card id = {uniqid()} name = "Natural Stones" description = {desc} tags = {tags} child = "2"/>
          <Card id = {uniqid()} name = "Marble" description = {desc} tags = {tags} child = "5"/> 
          <Card id = {uniqid()} name = "Agriculture Produce" description = {desc} tags = {tags} child = "3"/>
          </div> 
      </div>
    </div>
</div>);
}